@import '../../../../scss/variables';
@import '../../../../scss/mixins';

.homePage {
    @include box-size(100%, 100%);
    @include flex(auto, center, column);
    position: absolute;
    top: 30%;

    .title {
        font-size: 8rem;
        font-weight: bold;
        -webkit-text-stroke-width: 4px;
        -webkit-text-stroke-color: $black;

        //D
        span:first-child {
            color: #f6416c;
        }

        //o
        span:nth-child(2) {
            color: #f8f3d4;
        }

        //o
        span:nth-child(3) {
            color: #ffde7d;
        }

        //d
        span:nth-child(4) {
            color: #f6416c;
        }

        //l
        span:nth-child(5) {
            color: #00b8a9;
        }

        //e
        span:nth-child(6) {
            color: #f8f3d4;
        }
    }

    .input {
        @include box-size(35%, 32px);
        min-width: 420px;
        border: 3px solid $black;
        background-color: $lightGray;
    }

    .buttons {
        margin-top: 2rem;

        .searchButton,
        .luckyButton {
            border: 3px solid $black;
            padding: .5rem 2rem;
            color: $black;
            font-weight: bold;
            font-size: .9rem;
        }

        .searchButton {
            margin-right: .5rem;
        }

        .luckyButton {
            margin-left: .5rem;
        }
    }
}