@import '../../scss/variables';
@import '../../scss/mixins';

$tabBarHeight: $windowToolbarHeight;

.browser {
    @include box-size(100%, 100%);
    @include flex(auto, auto, column);
    background: $black;

    .tabs {
        @include box-size(auto, $tabBarHeight - 2px);
        @include flex();
        position: absolute;
        top: -$tabBarHeight + 2px;
        left: 6rem;
    }

    .toolBar {
        @include flex(default, center);

        background-color: $darkPurple;

        .toolBarIcons {
            @include flex(space-between);
            flex: 1;
            padding: .8rem 1rem;
        }

        .urlBarContainer {
            @include flex(default, center);
            flex: 12;
            position: relative;

            .httpsIcon {
                position: absolute;
                left: .5rem;
                font-size: .8rem;
                color: $green;
            }

            .urlBar {
                @include box-size(100%, $tabBarHeight - 8px);
                padding-left: 2rem;
                padding-right: 1rem;
                border-radius: 20px;
                border: none;
                background-color: $white;
                font-weight: bold;
            }
        }

        .toolBarIcon {
            color: $white;
            transition: all .2s cubic-bezier(0.785, 0.135, 0.15, 0.86);

            &:hover {
                color: $lightGray;
                transform: scale(1.5);
            }
        }

        .settingsIcon {
            margin: 0 1rem;
        }
    }

    .viewport {
        @include box-size(100%, 100%);
        @include flex();
        padding: .5rem;
        background-color: $white;
    }
}